html,
body {
  height: 100%;
  box-sizing: border-box;
  font-family: "Open Sans", sans-serif; }

.container {
  margin: 0 auto;
  box-sizing: border-box;
  max-width: 1180px;
  padding: 0 10px; }

.button {
  border: none;
  cursor: pointer;
  padding: 0;
  font: inherit;
  width: 240px;
  height: 60px;
  box-shadow: 0 1px 0 rgba(0, 0, 0, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.4);
  border-radius: 30px;
  color: #fff;
  font-size: 1.125rem;
  font-weight: 700;
  margin-top: 50px; }
  .button_velvet {
    background-color: #8824cd; }
    .button_velvet:hover {
      background-color: #9027d9; }

.header {
  height: 100%;
  background: url("../img/header_bg.jpg") no-repeat 50% 50%;
  background-size: cover;
  position: relative; }
  .header:after {
    content: "";
    position: absolute;
    width: 100%;
    height: 1px;
    top: 105px;
    left: 0;
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
    box-sizing: border-box; }
  .header__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 105px; }
  .header__logo {
    flex: 0 0 180px;
    display: block; }
    .header__logo-img {
      width: 100%;
      height: auto;
      display: block; }
  .header__menu {
    flex: 1 0 480px;
    display: flex;
    justify-content: flex-end;
    font-size: 0.8125rem;
    font-weight: 400;
    line-height: 24px;
    margin: 15px; }
    .header__menu-item {
      color: #fff;
      text-decoration: none;
      position: relative;
      margin: 0 3.5%; }
      .header__menu-item:after {
        position: absolute;
        content: "";
        width: 100%;
        height: 3px;
        background-color: #fff;
        bottom: -40px;
        left: 50%;
        transform: translateX(-50%) scale(0);
        transition: 0.3s; }
      .header__menu-item:hover:after {
        transform: translateX(-50%) scale(1); }
  .header__search {
    position: relative; }
    .header__search-field {
      flex: 0 0 220px;
      height: 45px;
      border-radius: 25px;
      font-size: 12px;
      font-weight: 400;
      border: 1px solid #f2f2f2;
      line-height: 4.625;
      padding: 0 30px;
      color: #626262;
      box-sizing: border-box; }
    .header__search:after {
      content: "";
      position: absolute;
      transform: translateY(-50%);
      top: 50%;
      right: 15px;
      width: 14px;
      height: 14px;
      background-image: url("../img/fa-search.png"); }
  .header__bottom {
    max-width: 520px;
    width: 100%;
    text-align: right;
    margin-top: 275px;
    color: #fff;
    font-weight: 400; }
  .header__title {
    font-size: 3.125rem;
    line-height: 4.375rem; }
  .header__text {
    max-width: 230px;
    height: 13px;
    overflow: hidden;
    font-size: 12px; }
